import * as React from 'react';
import './index.scss';
import { Modal, Tabs, Button, Icon, Select, Avatar, Row, Col } from 'antd';
import allPuzzles from '../../../utils/puzzle_data';
const Puzzle = require('react-image-puzzle');

const PIC_NUM = 87;
const defaultImg = require(`../../../images/puzzle/${Math.floor(Math.random() * PIC_NUM)}.jpg`)
const TabPane = Tabs.TabPane;
const Option = Select.Option;
const PuzzleImage = Puzzle.default;
const puzzles = allPuzzles;

interface IState {
    img: ImageData,
    size: number,
    finished: boolean
}

export default class MemberImagePuzzle extends React.Component<{}, IState>{
    constructor(props: {}) {
        super(props);
        this.state = {
            img: defaultImg,
            size: 500,
            finished: false
        }
    };

    public render() {
        const { img, size, finished } = this.state;
        const options = [];
        for (let i = 0; i < PIC_NUM; i++) {
            options.push(
                <Option key={i.toString()} value={i}>
                    <Avatar shape="square" size='small' src={
                        require(`../../../images/puzzle/${i}.jpg`)
                    } />
                </Option>
            )
        }
        if (finished) {
            return (
                <div>
                    <Button type="danger" onClick={this.restartPuzzle}></Button>
                    <Tabs
                        defaultActiveKey="1"
                        tabPosition='top'
                        style={{ minWidth: 700, height: size + 100, textAlign: 'center' }}
                    >
                        {
                            puzzles.map(puzzle => {
                                return (
                                    <TabPane tab={puzzle.tab} key={puzzle.key}>
                                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                                            <PuzzleImage image={img} size={size} level={puzzle.level} onDone={this.onSuccess} />
                                        </div>
                                    </TabPane>)
                            })
                        }
                    </Tabs>
                </div>
            )
        } else {
            return (
                <div>
                    <Row>
                        <Col span={22}>
                            <Button type="primary" style={{ marginRight: 5 }} onClick={this.changePuzzle}></Button>
                            <Button type="primary" style={{ marginRight: 5 }} onClick={() => this.scalePuzzle('plus')}><Icon type="zoom-in" /></Button>
                            <Button type="primary" onClick={() => this.scalePuzzle('reduce')}><Icon type="zoom-out" /></Button>
                        </Col>
                        <Col span={2}>
                            <Select placeholder='' style={{ width: 100 }} onChange={this.selectPuzzle}>
                                {
                                    options.map(optopn => {
                                        return optopn
                                    })
                                }
                            </Select>
                        </Col>
                    </Row>
                    <Tabs
                        defaultActiveKey="1"
                        tabPosition='top'
                        style={{ minWidth: 700, height: size + 100, textAlign: 'center' }}
                    >
                        {
                            puzzles.map(puzzle => {
                                return (
                                    <TabPane tab={puzzle.tab} key={puzzle.key}>
                                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                                            <PuzzleImage image={img} size={size} level={puzzle.level} onDone={this.onSuccess} />
                                        </div>
                                    </TabPane>)
                            })
                        }
                    </Tabs>
                </div>
            )
        }
    };

    private changePuzzle = () => {
        this.setState({
            img: require(`../../../images/puzzle/${Math.floor(Math.random() * PIC_NUM)}.jpg`)
        })
    };

    private selectPuzzle = (val: string) => {
        this.setState({
            img: require(`../../../images/puzzle/${val}.jpg`)
        })
    };

    private scalePuzzle = (type: string) => {
        if (type === 'plus') {
            this.setState({
                size: this.state.size + 50
            })
        } else {
            this.setState({
                size: this.state.size - 50
            })
        }
    };

    private restartPuzzle = () => {
        window.location.reload()
    }

    private onSuccess = () => {
        Modal.confirm({
            title: '',
            content: '~',
            okText: '',
            cancelText: '',
            onOk: () => {
                this.setState({
                    finished: true
                })
            },
            onCancel: () => {
                this.restartPuzzle()
            }
        })
    }
}


